.flat {
        .status-bg{
            fill: #b278d4;
        }
    

    &.action {
        .status-bg {
            fill: #80d34e;
        }
    }

    &.sold {
        .status-bg {
            fill: #d34e4e;
        }
    }

    &.booking {
        .status-bg {
            fill: #6b68ff;
        }
    }

    &.active {
        .plan{
            fill: #4a8a489a;
            stroke: rgb(103, 214, 103);
            stroke-width: 5px;
        }
    }
}

.plan {
    fill: rgba(127, 255, 212, 0.123);
    stroke: rgba(0, 255, 255, 0);
    cursor: pointer;

    &:hover {
        fill: rgba(26, 202, 26, 0.274);
        stroke: rgb(103, 214, 103);
        stroke-width: 4px;
    }
}

.flat-info{
    display: flex;
    padding: 36px 24px;
}

.floor-plan{
    max-width: 1440px;
    margin: 60px auto 60px 0;
}

.flat-item{
    padding: 20px;
    border: 1px solid;
    text-align: center;
    font-size: 20px;
    line-height: 1;
    display: grid;
    b{
        font-size: 24px;
        margin-top: auto;
        padding-top: 18px;
        color: blue;
    }
}
