.genplan {
    min-height: calc((100vh - 200px));
    display: flex;

    &-info {
        @extend .build-info;
    }

    .genplan-info{
        width: 100%;
        margin: 60px 0 60px 30px;
    }



}

.build {
    fill: #bdbdbd;
    stroke: #000;
    cursor: pointer;
    fill-opacity: 0.3;
    stroke-opacity: 0.5;
    stroke-width: 3px;
    transition: .3s;

    &:hover {
        fill-opacity: 0.8;
        stroke-opacity: 1;
    }
    &:active{
        fill: blue;
    }

}

.build-1 {
    fill: #A32626;
    stroke: #C92626;
}

.build-2 {
    fill: #56BE25;
    stroke: #5AC926;
}

.build-3 {
    fill: #316ECA;
    stroke: #2667C9;
}

.build-info {
    width: 650px;
    margin: 60px 30px 60px 30px;
    border-radius: 25px;
    padding: 60px;

    &-title {
        margin-bottom: 24px;
        text-align: center;
        font-weight: 600;
    }

    h4 {
        display: flex;
        margin-bottom: 12px;
        border-radius: 10px;
        padding: 18px 12px;

        span {
            display: inline-block;
            margin-left: 24px;
            font-weight: 900;
            color: #C92626;
        }
    }
    &-description{
        margin-top: 24px;
        margin-bottom: 12px;
        border-radius: 10px;
        padding: 18px 12px;
    }
}

h1,
h2,
h3,
h4,
h5,
h6 {
    line-height: 1.3;
}

h1 {
    font-size: 36px;
}

h2 {
    font-size: 32px;
}

h3 {
    font-size: 26px;
}

h4 {
    font-size: 22px;
}

h5 {
    font-size: 20px;
}

h6 {
    font-size: 18px;
}
