.build-item{
    @extend .genplan;

    .floor-info-description{
        margin-top: 24px;
        margin-bottom: 12px;
        border-radius: 10px;
        padding: 18px 12px;
    }
}
.building-info{
    @extend .genplan-info
}

.floor{
    stroke: rgba(51, 255, 0, 0);
    fill: #00000000;

    &:hover{
        fill: rgba(51, 255, 0, 0.363);
        stroke: rgba(51, 255, 0, 0.842);
    }
}
h3{
    margin-bottom: 24px;
}

.flats-info-item{
    display: flex;
    margin-bottom: 12px;
    border-radius: 10px;
    padding: 18px 12px;

    span{
        display: inline-block;
        margin-left: 24px;
        font-weight: 900;
        color: #C92626;
    }
}
